  padding-left: 28px;
  margin-bottom: 10px;
  position: relative;
}

.uk-list-disc li {
  margin-bottom: 10px;
}

.uk-list-disc li::before {
  position: absolute;
  left: 0;
  top: 13px;
  width: 6px;
  height: 6px;
  background-color: #1b365d;
  border-radius: 50%;
  content: '';
  list-style-type: none;
}

.uk-list-disc li a::after {
  left: 24px;
  width: calc(100% - 24px);
}

.uk-list-disc strong {
  font-weight: 700;
}

ol {
  padding-left: 0;
  counter-reset: item;
}

ol > li:last-of-type {
  margin-bottom: 25px;
}

ol > li strong {
  font-weight: 700;
}

ol > li::before {
  content: counter(item) ". ";
  counter-increment: item;
  left: 0;
  position: absolute;
  font-weight: normal;
}

