html,
body {
  overflow-x: hidden;
  font-family: "TT Norms", sans-serif;
  font-feature-settings: 'liga' off;
  width: 100%;
  position: relative;
  font-size: 16px;
}

.bg-black {
  background-color: #000;
}

.color-black {
  color: #000;
}

.bg-white {
  background-color: #fff;
}

.color-white {
  color: #fff;
}

a,
button {
  appearance: none;
  outline: none;
  transition: 256ms ease-in-out;
}

a:hover, a:focus, a:active {
  text-decoration: none;
}

button,
input[type='button'],
input[type='submit'] {
  background: none;
  border: 0;
  cursor: pointer;
  outline: none;
  padding: 0;
}

.uk-button {
  font-weight: 700;
  font-size: 14px;
  color: #fff;
  border-radius: 50px;
  text-align: left;
  line-height: 1.3em;
  border: 0;
  padding: 15px 35px;
  transition: 256ms ease-in-out;
  text-transform: none;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.uk-button * {
  transition: inherit;
}

.uk-button.uk-button-primary, .uk-button.uk-button-default, .uk-button.uk-button-secondary, .uk-button.uk-button-danger {
  border-radius: 50px;
}

.uk-button.uk-button-primary, .uk-button.uk-button-secondary {
  background-color: #267cb9;
}

.uk-button.uk-button-danger {
  background-color: #f2827f;
}

.uk-button > span:last-of-type {
  display: inline-flex;
  align-items: center;
  position: relative;
  margin-left: 15px;
}

.uk-button > span:last-of-type::after {
  position: absolute;
  left: 0;
  height: 1px;
  background-color: #fff;
  content: '';
  width: 0;
